.reviews{
  &__inner{
    position: relative;
  }
  @media screen and (min-width: 993px){
    .arrow-link{
      position: absolute;
      top: 0;
      right: 9.75em;
      height: 3em;
      min-width: unset;
      svg{
        display: none;
      }
    }
  }
}

.reviews-slider{
  margin-top: 1.875em;
  .swiper{
    &-container{
      width: calc(100% + 1.625em);
    }
    &-slide{
      padding-right: 1.625em;
    }
    &-nav{
      position: absolute;
      right: 0;
      top: 0;
    }
    &-button{
      margin: 0 0 0 .5em;
      position: static;
    }
    &-pagination{
      display: none;
    }
  }
}

.slider__wrap{
  overflow: hidden;
}

.swiper-nav{
  display: flex;
  align-items: center;
}

.swiper-button{
  border: 2px solid #B0B4C7;
  width: 4.25em;
  height: 3em;
  border-radius: 2.5em;
  @include center;
  svg{
    width: 2.125em;
  }
  &:hover:not(.swiper-button-disabled){
    background-color: $primary;
    border-color: $primary;
    path{
      stroke: $white;
    }
  }
}

.swiper-pagination{
  @include center;
  z-index: 1;
  &-bullet{
    width: .375em;
    height: .375em;
    border-radius: 50%;
    background-color: #CED1DD;
    opacity: 1;
    margin: 0 .1875em !important;
    &-active{
      background-color: $black;
    }
  }
}

.review-card{
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    margin-right: 1.25em;
  }
  &__name{
    font-size: 1.0625em;
    line-height: 140%;
    font-weight: 700;
    color: $primary;
  }
  &__date{
    white-space: nowrap;
    @include center;
    padding: 0 .75em;
    height: 1.625em;
    border: 1px solid #B3B9CF;
    border-radius: 2.5em;
    color: #B0B4C7;
    margin-left: .75em;
    span{
      font-size: .75em;
      letter-spacing: .02em;
    }
  }
  &__bottom{
    margin-left: auto;
    margin-top: .25em;
    width: calc(100% - 6.25em);
    border: 1px dashed #9DA3BB;
    border-radius: 0 2.1875em 2.1875em 2.1875em;
    padding: 1.625em;
  }
  &__text{
    @include truncate(6);
    font-size: 1.125em;
    line-height: 150%;
    font-weight: 500;
  }
  &__more{
    font-size: .875em;
    line-height: 140%;
    font-weight: 600;
    color: #B0B4C7;
    margin-top: 1.25em;
  }
}

.avatar{
  width: 5em;
  height: 5em;
  border-radius: 50%;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
  }
}

.arrow-link{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1.25em;
  min-width: 17.5em;
  svg{
    width: 1.8125em;
  }
}


@media screen and (max-width: 992px){
  .reviews{
    .arrow-link{
      margin-top: 1.875em;
    }
  }

  .reviews-slider{
    margin-top: 1.375em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        padding-right: 1em;
      }
      &-nav{
        display: none;
      }
      &-pagination{
        display: flex;
        margin-top: .75em;
        position: static;
      }
    }
  }

  .review-card{
    &__img{
      margin-right: .75em;
    }
    &__info{
      position: relative;
    }
    &__name{
      font-size: 1em;
      line-height: 120%;
      max-width: 10em;
    }
    &__date{
      position: absolute;
      top: 100%;
      margin-top: .375em;
      height: 1.5em;
      padding: 0 .625em;
      span{
        font-size: .6875em;
      }
    }
    &__bottom{
      margin-top: 1.875em;
      width: 100%;
      padding: 1.875em 1.25em 1.25em;
      border-radius: 0 1.5625em 1.5625em 1.5625em;
    }
    &__text{
      font-size: .9375em;
      -webkit-line-clamp: 8;
    }
    &__more{
      font-size: .8125em;
      margin-top: 1em;
    }
  }

  .arrow-link{
    height: 3.125em;
    span{
      font-size: .9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .reviews-slider{
    margin-top: .75em;
  }
}
