.news2{
  .blog-card{
    &__img{
      border-radius: .625em;
    }
  }
}

.partners{
  &__items{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 1em;
  }
  &__item{
    margin-top: 1em;
    img{
      max-width: 11em;
      max-height: 4em;
      display: block;
    }
  }
}

.offers{
  padding-bottom: 7.5em;
  &__inner{
    padding: 3em 2.75em 2.5em;
    background-color: $primaryLight;
    border-radius: 2.1875em;
  }
  &__top{
    font-size: 2.125em;
    line-height: 125%;
    font-weight: 500;
    max-width: 25.6em;
  }
  &__wrap{
    margin-top: 6.5em;
    display: flex;
    align-items: center;
  }
  &__img{
    width: 10em;
    height: 10em;
    margin-right: 1.5em;
  }
  &__info{
    font-weight: 500;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__name{
    font-size: 1.4375em;
    line-height: 130%;
  }
  &__prof{
    font-size: .9375em;
    line-height: 120%;
    color: #919194;
  }
  &__phone{
    font-size: 2.5em;
    line-height: 130%;
    margin-top: .35em;
  }
  &__mail{
    font-size: 1em;
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .partners{
    &__items{
      margin-top: 0;
    }
    &__item{
      img{
        max-width: 8em;
        max-height: 3em;
      }
    }
  }

  .offers{
    padding-bottom: 5em;
    &__inner{
      padding: 2.1875em 1.875em 2.5em;
      border-radius: 1.5625em;
    }
    &__top{
      font-size: 1.4375em;
    }
    &__wrap{
      margin-top: 3.375em;
    }
    &__img{
      width: 7.5em;
      height: 7.5em;
      margin-right: 1em;
    }
    &__name{
      font-size: 1.125em;
    }
    &__prof{
      font-size: .6875em;
    }
    &__phone{
      font-size: 1.625em;
      margin-top: .2em;
    }
    &__mail{
      font-size: .6875em;
    }
  }
}


@media screen and (max-width: 576px){
  .partners{
    &__items{
      margin-top: 1.25em;
      display: grid;
      grid-template-columns: 50% 50%;
      gap: 1.75em 0;
    }
    &__item{
      margin: 0;
      img{
        max-width: 100%;
        max-height: 4em;
      }
    }
  }

  .offers{
    &__inner{
      padding: 1.625em .75em 1.5em;
      border-radius: 1.25em;
    }
    &__top{
      font-size: 1.125em;
      line-height: 117%;
    }
    &__wrap{
      margin-top: 2.25em;
      align-items: flex-start;
    }
    &__img{
      width: 5em;
      height: 5em;
      margin-right: .75em;
    }
    &__info{
      padding-top: .375em;
    }
    &__name{
      font-size: 1.25em;
    }
    &__prof{
      font-size: .8125em;
    }
    &__phone{
      font-size: 1.4375em;
      margin-top: .33em;
    }
    &__mail{
      font-size: .8125em;
    }
  }
}
