.timetable{
  padding-bottom: 7.5em;
}

.schedule-table{
  margin-top: 3.125em;
  &__head{
    padding-bottom: 1.25em;
    border-bottom: 1px solid #E4E3EA;
    height: 4.75em;
    display: grid;
    grid-template-columns: 16.875em 14.375em calc(100% - 31.25em);
    align-items: center;
    &-title{
      font-size: 1em;
      line-height: 130%;
      font-weight: 600;
      color: #4F5366;
    }
  }
  &__items{
    margin-top: 2.5em;
    @include grid100gap(2.25em);
  }
}

.week{
  padding: 0 2.25em;
  position: relative;
  &__items{
    display: grid;
    grid-template-columns: repeat(7,calc(100% / 7 - 3.75em / 7));
    gap: .625em;
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.75em;
    height: 1.75em;
    background-color: #EEF2FA;
    border-radius: 50%;
    @include center;
    svg{
      width: .4375em;
    }
    &--prev{
      left: 0;
    }
    &--next{
      right: 0;
    }
    &:hover{
      background-color: $black;
      path{
        stroke: #fff;
      }
    }
  }
}

.week-item{
  height: 3.5em;
  background-color: #E4E6EC;
  padding: 0 1em;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: .625em;
  color: #4F5366;
  cursor: pointer;
  &__title{
    font-size: .9375em;
    line-height: 105%;
    font-weight: 600;
  }
  &__num{
    font-size: .75em;
    line-height: 120%;
    margin-top: .35em;
    font-weight: 500;
  }
  &._week-end{
    background-color: #EEF3FA;
    .week-item__num{
      color: #E58383;
    }
  }
}

.schedule-item{
  display: grid;
  grid-template-columns: 16.875em 14.375em calc(100% - 31.25em);
  align-items: center;
  &__prof,
  &__name{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
  }
  &__name{
    color: $primary;
    a{
      text-decoration: underline;
    }
  }
  &__prof{
    color: #4F5366;
  }
  &__days{
    padding: 0 2.25em;
    display: grid;
    grid-template-columns: repeat(7,calc(100% / 7 - 3.75em / 7));
    gap: .625em;
  }
}

.schedule-day{
  &__info{
    display: none;
  }
  &__time{
    padding: .375em;
    @include center;
    text-align: center;
    height: 3.5em;
    background-color: #E6F8F7;
    border-radius: .625em;
    color: #737F8E;
    font-weight: 600;
    span{
      font-size: 1em;
      line-height: 115%;
      display: block;
    }
    i{
      display: none;
    }
    &--empty{
      background-color: transparent;
      span{
        display: none;
      }
    }
  }
}

.timetable-filter{
  .fg{
    width: 24.25em;
    margin-right: 1.125em;
    &:last-child{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .schedule-table{
    margin-top: 2em;
    &__head{
      grid-template-columns: 100%;
      border-bottom: none;
      padding-bottom: 0;
      height: unset;
      &-col,
      &-col2{
        display: none;
      }
    }
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 3.125em 1.25em;
    }
  }

  .week-item{
    padding: 0 .5em;
    height: 3.375em;
  }

  .schedule-item{
    grid-template-columns: 100%;
    gap: .375em;
    &__name{
      max-width: 17em;
    }
    &__prof{
      font-size: .8125em;
    }
    &__days{
      margin-top: 1em;
      grid-template-columns: 100%;
      padding: 0;
    }
  }

  .schedule-day{
    display: grid;
    grid-template-columns: auto 15em;
    justify-content: space-between;
    align-items: center;
    &__info{
      display: block;
      &-title{
        font-size: .8125em;
        line-height: 110%;
        font-weight: 600;
        color: #4F5366;
      }
      &-num{
        font-size: .6875em;
        line-height: 110%;
        font-weight: 500;
        color: #4F5366;
      }
    }
    &__time{
      height: 2.5em;
      br{
        display: none;
      }
      &-num{
        span{
          display: inline;
          font-size: .9375em;
        }
        i{
          display: inline-block;
          padding: 0 .125em;
        }
      }
      &--empty{
        background-color: #F3F5FB;
        color: #9DA8B6;
        span{
          display: block;
        }
      }
    }
  }

  .timetable-filter{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: .75em 1.25em;
    .fg{
      width: 100%;
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .schedule-table{
    &__items{
      grid-template-columns: 100%;
      gap: 2.25em;
    }
  }

  .week{
    padding: 0;
    &__items{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: unset;
      margin-top: -1em;
      width: calc(100% + 1em);
    }
    &__btn{
      position: static;
      margin: 0 .5em;
      transform: unset;
    }
    &__nav{
      display: flex;
      justify-content: center;
      margin: 1em 0 0;
    }
  }

  .week-item{
    margin: 1em 1em 0 0;
    width: calc(100% / 3 - 1em);
  }

  .timetable-filter{
    grid-template-columns: 100%;
  }
}
