.header{
  padding: .625em 0;
  position: relative;
  z-index: 5;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10.125em;
    margin-right: 2.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    font-size: 1.25em;
    line-height: 145%;
    font-weight: 500;
    margin-left: .75em;
  }
  &._open{
    .desktop-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .burger{
      border-color: #5B5F74;
      background-color: transparent;
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
        width: 1.25em;
      }
      path{
        stroke: #5B5F74;
      }
    }
  }
  &._open-search{
    &:after{
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: $vh;
      background-color: rgba(#000,.6);
      pointer-events: none;
    }
    .header-search{
      &__field{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.burger,
.header-search__btn{
  width: 3.125em;
  height: 3.125em;
  border-radius: 50%;
  padding: 0;
}

.burger{
  margin-left: .625em;
  position: relative;
  svg{
    width: 1.625em;
    @include centerAbsolute;
  }
  ._view2{
    opacity: 0;
  }
  &:hover{
    path{
      fill: $white;
    }
  }
}

.nav-item{
  margin-right: 1.5em;
  position: relative;
  &:hover{
    .nav-item__show{
      color: $black;
      svg{
        transform: rotate(-180deg);
      }
      path{
        fill: $black;
      }
    }
    .nav-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    color: #818494;
    transition: all .3s;
    cursor: pointer;
    svg{
      width: .5625em;
      margin-left: .125em;
    }
    span{
      font-size: 1.0625em;
      line-height: 145%;
    }
  }
  &__hidden{
    position: absolute;
    top: 98%;
    left: -1em;
    padding-top: 2em;
    z-index: 3;
    transition: all .5s;
    opacity: 0;
    pointer-events: none;
    min-width: 12.25em;
  }
  &__inner{
    padding: 1.5em 1.25em;
    background-color: $white;
    border-radius: 2.1875em;
    box-shadow: 0 .125em 1.5em rgba(0, 0, 0, 0.12);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__sub-link{
    font-size: 1.0625em;
    line-height: 180%;
    color: $primary;
    white-space: nowrap;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1.0625em;
    line-height: 145%;
    color: #818494;
    margin-right: 1.5em;
  }
}

.header-action{
  display: flex;
  margin-left: auto;
  &__btn{
    height: 3.125em;
    padding: 0 .75em;
  }
}

.index-page{
  .header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: $white;
    border-radius: 0 0 3.125em 3.125em;
  }
}

.header-search{
  margin-left: .625em;
  position: relative;
  &__field{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    width: 40.25em;
    height: 3.125em;
    top: 0;
    right: 0;
    input{
      height: 100%;
      background-color: $white;
      font-size: 1em;
      border-radius: 1.5em;
    }
  }
  &__btn{
    img{
      width: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      justify-content: flex-end;
    }
    &__logo{
      margin-right: auto;
    }
  }

  .nav{
    display: none;
  }

  .header-search,
  .header-action{
    margin-left: .625em;
  }

  .index-page{
    .header{
      border-radius: 0 0 1.25em 1.25em;
      &._open{
        border-radius: 0;
      }
    }
  }

  .burger{
    margin-left: .5em;
  }
}


@media screen and (max-width: 576px){
  .header{
    height: 4em;
    display: flex;
    align-items: center;
    &__logo{
      width: 7.5625em;
    }
    &__phone{
      display: none;
    }
    &._open-submenu{
      .desktop-menu{
        overflow: hidden;
      }
    }
  }

  .header-action{
    &__btn{
      display: none;
    }
  }

  .header-search__btn,
  .burger{
    width: 2.75em;
    height: 2.75em;
  }

  .header-search{
    &__field{
      width: 19em;
      height: 2.75em;
    }
  }
}
