@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt40{
  margin-top: 2.5em;
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    background-color: $white;
    @include lastM(0 .5em 0 0);
    @include center;
    flex-shrink: 0;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1.5em;
    }
  }
  &._type2{
    .socials__item{
      width: 6.375em;
      height: 6.375em;
      background-color: #E6F8F7;
      @include lastM(0 1em 0 0);
      svg{
        width: 3.125em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  ._mt16-tablet{
    margin-top: 1em;
  }

  .socials{
    &__item{
      @media screen and (min-width: 577px){
        width: 2.5em;
        height: 2.5em;
        @include lastM(0 .35em 0 0);
        svg{
          width: 1.25em;
        }
      }
    }
    &._type2{
      .socials__item{
        width: 3.5em;
        height: 3.5em;
        @include lastM(0 .75em 0 0);
        svg{
          width: 1.75em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  ._mt12-mob{
    margin-top: .75em;
  }

  .socials{
    &._type2{
      .socials__item{
        width: 2.75em;
        height: 2.75em;
        @include lastM(0 .5em 0 0);
        svg{
          width: 1.375em;
        }
      }
    }
  }
}
