.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .3;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 26.125em;
        max-width: calc(100% - 1.5em);
        position: relative;
        z-index: 3;
        padding: 1.5em;
        border-radius: 1.25em;
        display: inline-block;
    }
    &__top{
        padding-right: 3.75em;
        padding-top: .3125em;
        position: relative;
        min-height: 3.125em;
    }
    &__title{
        font-size: 1.875em;
        line-height: 110%;
        font-weight: 600;
    }
    &__desc{
        font-size: .9375em;
        line-height: 120%;
        color: #5b5f74;
        font-weight: 500;
        opacity: .7;
        margin-top: .875em;
    }
    .close-btn{
        position: absolute;
        top: 0;
        right: 0;
    }
}

.close-btn{
    width: 3.125em;
    height: 3.125em;
    border: 1px solid #d7d7d7;
    border-radius: 50%;
    @include center;
    svg{
        width: 1em;
    }
    &:hover{
        border-color: $black;
        background-color: $black;
        rect{
            fill: $white;
        }
    }
}

.modal-form{
    margin-top: 2em;
    &__fields{
        @include grid100gap(.875em);
    }
    &__btn{
        margin-top: 2.5em;
        width: 100%;
    }
}

.thank-modal{
    .modal__top{
        margin-bottom: 2.75em;
    }
    .modal__title{
        text-align: center;
    }
    &__btn{
        width: 12.5em;
        margin: 2em auto 0;
    }
}

.req-modal{
    .modal__dialog{
        width: 45em;
    }
    .modal__desc{
        max-width: 32.3em;
    }
    .consult-form2{
        max-width: 100%;
    }
    .m-check{
        max-width: 18.75em;
    }
}

.thank-modal2{
    .modal__dialog{
        width: 45em;
        padding-top: 3em;
        padding-bottom: 3em;
    }
    .modal__top{
        margin-bottom: 1em;
    }
    .modal__desc{
        font-size: 1.0625em;
        text-align: center;
        margin-top: 1.25em;
    }
    .thank-modal__btn{
        width: 7.5em;
        margin-top: 1.875em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__title {
            font-size: 1.625em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &:after{
            display: none;
        }
        &__top{
            padding-right: 3.125em;
        }
        &__desc{
            font-size: .75em;
            line-height: 125%;
        }
        &__dialog {
            padding: 2em .75em;
        }
    }

    .close-btn{
        width: 2.75em;
        height: 2.75em;
    }

    .req-modal{

    }
}
