.info-page{
  padding-bottom: 7.5em;
  &__items{
    @include grid100gap(3.125em);
  }
}

.info-card{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 64.5em;
  &__title{
    font-size: 2.1875em;
    line-height: 120%;
    font-weight: 500;
    a{
      text-decoration: underline;
    }
  }
  .blog-card__date{
    margin-top: 1.5em;
  }
  .text{
    color: $black;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .info-page{
    padding-bottom: 5em;
    &__items{
      gap: 2.5em;
    }
  }

  .info-card{
    &__title{
      font-size: 1.875em;
      line-height: 110%;
    }
  }
}


@media screen and (max-width: 576px){
  .info-page{
    &__items{
      gap: 2em;
    }
  }

  .info-card{
    &__title{
      font-size: 1.5625em;
      line-height: 100%;
    }
    .blog-card__date,
    .text{
      margin-top: 1em;
    }
  }
}
