.reviews-page{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 3.125em 1.25em;
  }
  .review-card{
    &__name{
      color: $black;
    }
    &__text{
      display: block;
    }
    &__more{
      display: none;
    }
    &__info{
      position: relative;
    }
    &__date{
      position: absolute;
      top: 100%;
      margin-top: .375em;
      height: 1.5em;
      padding: 0 .625em;
      margin-left: 0;
      span{
        font-size: .6875em;
      }
    }
    &__bottom {
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews-page {
    &__grid {
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }
}


