.m-section{
  padding: 3em 0;
}

.services{

}

.all-link,
.service-card{
  border-radius: 2.1875em;
  padding: 1.5em 1.875em 1.875em;
  min-height: 24.125em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.service-card{
  background-color: $primaryLight;
  color: $primary;
  position: relative;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    color: $white;
    .service-card__icon{
      path{
        stroke: #b6f3f2;
      }
    }
    .service-card__num{
      opacity: 0;
    }
    .service-card__btn{
      opacity: 1;
    }
  }
  &__num{
    background-color: $white;
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    @include center;
    font-weight: 600;
    transition: all .3s;
  }
  &__icon{
    position: absolute;
    right: 1em;
    bottom: 2em;
    svg{
      max-width: 13em;
      max-height: 13.25em;
    }
  }
  &__btn{
    position: absolute;
    bottom: 1.875em;
    left: 1.75em;
    font-weight: 600;
    opacity: 0;

  }
  &._img-fix{
    .service-card__icon{
      right: 0;
    }
  }
}

.all-link{
  color: $primary;
  border: 1px solid $primary;
  &:hover{
    background-color: $primary;
    border-color: $primary;
    color: $white;
    .all-link__arrow{
      background-color: $white;
      border-color: $white;
      path{
        stroke: #B0B4C7;
      }
    }
  }
  &__arrow{
    transition: all .3s;
    margin-left: auto;
    border: 2px solid $primary;
    width: 4.25em;
    height: 3em;
    border-radius: 2.5em;
    @include center;
    svg{
      width: 2.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.5em 0;
  }

  .service-card,
  .all-link{
    min-height: 14.5em;
    border-radius: 1.25em;
    padding: 1.25em 1em 1em;
    &__title{
      font-size: 1.25em;
    }
  }

  .service-card{
    &__icon{
      right: 1em;
      bottom: 1em;
      svg{
        max-width: 7em;
        max-height: 7em;
      }
    }
    &__num{
      width: 1.875em;
      height: 1.875em;
      span{
        font-size: .8125em;
      }
    }
    &__btn{
      bottom: 1em;
      left: 1em;
      padding: 0 1em;
      height: 2.5em;
      span{
        font-size: .75em;
      }
    }
  }

  .all-link{
    &__arrow{
      width: 2.6875em;
      height: 1.875em;
      svg{
        width: 1.375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .service-card,
  .all-link{
    min-height: 10.625em;
    padding-bottom: .625em;
  }

  .service-card{
    &__icon{
      right: .625em;
      bottom: .625em;
      svg{
        max-width: 4.375em;
        max-height: 4.375em;
      }
    }
    &__title{
      font-size: 1em;
    }
  }
}
