.consult{
  padding-bottom: 9.375em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 44.25em) 44.25em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 3.125em;
    &._view2{
      display: none;
    }
  }
  &__col2{
    padding: 3.625em 3.75em;
    background-color: $primary;
    border-radius: 3.125em;
    width: calc(100% + 5.25em);
    margin-left: -5.25em;
    color: $white;
  }
  &__desc{
    font-size: .9375em;
    line-height: 140%;
    max-width: 23em;
    margin-top: 1.67em;
  }
}

.consult-form{
  margin-top: 2em;
  max-width: 33.125em;
  &__fields{
    @include grid100gap(2.5em);
  }
  &__hint{
    margin-top: 3.5em;
    max-width: 33em;
    font-weight: 600;
    .text14{
      line-height: 140%;
    }
  }
  &__nav{
    margin-top: 1.75em;
    display: flex;
  }
  &__btn{
    padding: 0 2.875em;
  }
}

.fg2{
  input{
    width: 100%;
    background-color: transparent;
    border-radius: 0;
    font-size: 1em;
    height: 2.75em;
    border: none;
    border-bottom: 1px solid $white;
    padding: 0;
    color: $white;
    font-weight: 500;
    &::placeholder{
      color: $white;
    }
  }
}


@media screen and (max-width: 992px){
  .consult {
    padding-bottom: 3.125em;
    &__inner {
      grid-template-columns: 27.375em calc(100% - 27.375em);
    }
    &__img{
      border-radius: 1.5625em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__col2{
      border-radius: 1.5625em;
      width: calc(100% + 4.625em);
      margin-left: -4.625em;
      padding: 2em;
    }
    &__desc{
      font-size: .75em;
      margin-top: 1.25em;
    }
  }

  .consult-form{
    margin-top: 2.125em;
    &__hint{
      margin-top: 2.25em;
      .text14{
        font-size: .6875em;
        line-height: 130%;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      height: 3.125em;
      padding: 0 1.75em;
      span{
        font-size: .8125em;
      }
    }
  }

  .fg2{
    input{
      font-size: .875em;
      height: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult {
    padding-bottom: 5em;
    &__inner {
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
    &__col2{
      border-top-left-radius: 0;
      width: 100%;
      margin-left: 0;
      padding: 2.1875em .75em;
    }
  }
}
