.vacancies-page{
  .text{
    h3{
      margin-bottom: .75em;
    }
    ol{
      li{
        &:before{
          color: inherit;
        }
      }
    }
  }
  &__hint{
    color: #FF5A5A;
    padding-left: 1.47em;
  }
}

.vacancy-req{
  padding: 4.375em 0 7.5em;
  &__inner{
    padding: 5em;
    background-color: $primaryLight;
  }
}

.vacancy-form{
  width: 45em;
  max-width: 100%;
  padding: 2.125em 1.5em 2.5em;
  border-radius: 1.25em;
  background-color: $white;
  margin: 0 auto;
  &__top{
    @include grid100gap(.875em);
    max-width: 31.25em;
    margin-bottom: 1.5em;
  }
  &__title{
    font-size: 1.875em;
    line-height: 110%;
    font-weight: 600;
  }
  &__desc{
    font-size: .9375em;
    line-height: 120%;
    color: #5B5F74;
    opacity: .7;
  }
  .fg{
    input,
    textarea{
      border-color: #CDCED8;
    }
  }
  .m-check{
    margin-top: 1.75em;
    max-width: 18.75em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .vacancy-req{
    margin: 3em 0 5em;
    padding: 3.75em 0;
    background-color: $primaryLight;
    &__inner{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .vacancy-req{
    margin-left: auto;
    margin-right: auto;
    width: calc(100% - 1.5em);
    padding: 1.875em 0;
    border-radius: 0 1.5625em 1.5625em 1.5625em;
  }

  .vacancy-form{
    padding: 0;
    background-color: transparent;
    &__desc{
      font-size: .75em;
    }
    .m-check{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }
}
