.prices{
  padding-bottom: 7.5em;
  padding-top: 1em;
  &__title{
    font-size: 2.1875em;
    line-height: 120%;
    font-weight: 600;
  }
  &__groups{
    margin-top: 2.25em;
    @include grid100gap(3.75em);
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    height: 2.5em;
    padding: 0 1.125em;
    span{
      font-size: .875em;
    }
  }
}

.price-group{
  &__title{
    font-size: 1.3125em;
    line-height: 145%;
    font-weight: 600;
  }
  &__items{
    margin-top: 1.375em;
    @include grid100gap(1.5em);
  }
}

.price-item{
  display: grid;
  grid-template-columns: auto 35em;
  justify-content: space-between;
  &__num,
  &__title{
    font-size: 1.1875em;
    line-height: 145%;
    color: #5B5F74;
  }
  &__col2{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__btn{
    padding: 0;
    width: 7em;
    height: 1.75em;
    font-weight: 600;
    margin-left: 4.625em;
    span{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 992px){
  .prices{
    padding-bottom: 5em;
    &__title{
      font-size: 1.875em;
    }
    &__groups{
      margin-top: 1.5em;
      gap: 2.125em;
    }
  }

  .price-group{
    &__title{
      font-size: 1.125em;
    }
    &__items{
      margin-top: 1em;
      gap: 1.25em;
    }
  }

  .price-item{
    grid-template-columns: auto 15em;
    &__num,
    &__title{
      font-size: .9375em;
      line-height: 120%;
    }
    &__col2{
      padding-left: 1em;
    }
    &__btn{
      width: 6em;
      height: 1.875em;
      margin-left: 2em;
      span{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .prices{
    &__title{
      font-size: 1.125em;
    }
  }

  .price-group{
    &__title{
      font-size: 1em;
    }
    &__items{
      gap: 1.5em;
    }
  }

  .price-item{
    grid-template-columns: auto 7em;
    &__num,
    &__title{
      font-size: .875em;
      line-height: 135%;
    }
    &__col{
      max-width: 14.375em;
    }
    &__col2{
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
    }
    &__btn{
      width: 100%;
      margin: .25em 0 0;
    }
  }
}
