.service-page{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 13.875em - 1.5em) 13.875em;
    justify-content: space-between;
  }
  &__hint{
    max-width: 48.75em;
    font-weight: 500;
    color: $primary;
  }
  .socials{
    position: sticky;
    top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1.25em 1em;
    &__item{
      margin: 0 !important;
    }
  }
  .text{
    img{
      width: calc(100% + 13.875em + 1.5em);
      display: block;
      border-radius: 2.1875em;
      margin: 2.625em 0;
    }
  }
  .service-group{
    margin: 1.5em 0;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: 2.1875em;
    margin-bottom: 3.375em;
    &._view2{
      display: none;
    }
  }
}

.services2{
  .services__grid{
    max-width: 75em;
  }
}

.consult2{
  padding: 6.25em 0 7.5em;
  &._pb48{
    padding-bottom: 3em;
  }
  &__inner{
    padding: 3em 2.5em;
    background-color: #E6F8F7;
    border-radius: 0 2.1875em 2.1875em 2.1875em;
    position: relative;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 36em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  .h3{
    br{
      display: none;
    }
  }
  &__desc{
    color: #5B5F74;
    font-size: .9375em;
    line-height: 125%;
    max-width: 32.6em;
    margin-top: 1em;
    font-weight: 500;
    opacity: .7;
  }
}

.consult-form2{
  margin-top: 1.625em;
  max-width: 42.125em;
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .4375em));
    gap: 1em .875em;
  }
  .m-check{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .service-page{
    &__grid{
      grid-template-columns: calc(100% - 8em) 7.5em;
    }
    &__hint{
      display: none;
    }
    .socials{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .625em .5em;
    }
    .text img,
    .service-group{
      width: calc(100% + 8em);
    }
    .text{
      img{
        border-radius: 1.5625em;
        margin: 2.125em 0;
      }
    }
    &__img{
      margin-bottom: 2.5em;
    }
  }

  .consult2{
    padding: 1.5em 0 5em;
    &__inner{
      padding: 2.1875em 1.875em;
    }
    &__img{
      width: 25.25em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .h3{
      br{
        display: unset;
      }
    }
    &__desc{
      font-size: .8125em;
      line-height: 130%;
      max-width: 25.3em;
      margin-top: 1.25em;
    }
  }

  .consult-form2{
    margin-top: 1.875em;
    max-width: 28.75em;
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .875em .75em;
    }
    .fg{
      textarea,
      input{
        border-color: #CDCED8;
      }
    }
    .m-check{
      margin-top: 1.25em;
      max-width: 20em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .service-page{
    &__grid{
      grid-template-columns: calc(100% - 3.25em) 2.75em;
    }
    .socials{
      grid-template-columns: 100%;
      gap: .5em;
    }
    .text img,
    .service-group{
      width: calc(100% + 3.25em);
    }
    .text{
      img{
        margin: 1.875em 0;
      }
    }
    &__img{
      border-radius: 1.5625em;
      margin-bottom: 2em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }

  .consult2{
    &__inner{
      padding: 1.875em .75em;
      border-radius: 0 1.5625em 1.5625em 1.5625em;
    }
    &__img{
      display: none;
    }
    .h3{
      font-weight: 500;
    }
    &__desc{
      font-size: .75em;
      line-height: 125%;
      margin-top: 1.5em;
    }
  }

  .consult-form2{
    margin-top: 1.375em;
    max-width: 100%;
    &__fields{
      grid-template-columns: 100%;
      gap: .625em;
    }
    .fg{
      textarea,
      input{
        border-color: #CDCED8;
      }
    }
    &__nav{
      margin-top: 1.125em;
    }
    &__btn{
      width: 100%;
      height: 3.125em;
      span{
        font-size: .9375em;
      }
    }
  }
}
