.appointment-doctors{
  margin-top: 3.125em;
}

.appointment-card{
  padding-bottom: 2.5em;
  margin-bottom: 2.5em;
  border-bottom: 1px solid #e6eaef;
  display: grid;
  grid-template-columns: calc(100% - 62.75em) 31.5em 30em;
  gap: .625em;
  &:last-child{
    padding-bottom: 0;
    margin-bottom: 0;
    border-bottom: none;
  }
  &__img{
    width: 6.875em;
    height: 6.875em;
    margin-bottom: 1.125em;
  }
  &__name{
    font-size: 1.0625em;
    line-height: 140%;
    font-weight: 700;
  }
  &__prof{
    font-weight: 500;
    font-size: .8125em;
    line-height: 120%;
    color: $primary;
    margin-top: .33em;
  }
}

.date-card{
  min-height: 100%;
  padding: 2em 2.5em;
  background: #f1f4f9;
  border-radius: 1.25em;
  &__title{
    font-weight: 500;
    font-size: 1.75em;
    line-height: 130%;
    color: #4f5366;
  }
  .air-datepicker{
    margin-top: 2em;
    background-color: transparent;
    padding: 0;
    border: none;
    width: 100%;
    font-size: 1em;
    &-nav{
      padding: 0;
      justify-content: unset;
      min-height: unset;
      border: none;
      &--title{
        font-weight: 600;
        font-size: 1.125em;
        line-height: 135%;
        color: #737f8e;
        i{
          display: none;
        }
      }
      &--action{
        width: 1.75em;
        height: 1.75em;
        background-size: .625em auto !important;
        background-repeat: no-repeat !important;
        background-position: center !important;
        &[data-action="prev"]{
          background-image: url(../img/prev.svg) !important;
        }
        &[data-action="next"]{
          background-image: url(../img/next.svg) !important;
        }
        svg{
          display: none;
        }
      }
    }
    &-body--day-names{
      grid-template-columns: repeat(7,calc(100% / 7 - 3.75em / 7));
      gap: .625em;
      margin: .75em 0;
    }
    &-body--day-name{
      font-size: 1.125em;
      font-weight: 600;
      color: #737F8E;
      text-transform: unset;
    }
    &-body--cells{
      margin-top: .5em;
      &.-days-{
        grid-auto-rows: unset;
        grid-template-columns: repeat(7,calc(100% / 7 - 3.75em / 7));
        gap: .625em;
      }
    }
    &-cell{
      height: 2.44em;
      background-color: $white;
      border: 2px solid $white;
      border-radius: 1.125em;
      font-weight: 600;
      font-size: 1.125em;
      &.-other-month-{
        color: rgba(#737f8e,.4);
      }
      &.-current-{
        border-color: $primary;
        color: #737f8e;
      }
      &.-selected-{
        border-color: $primary;
        background-color: $primary;
        color: $white;
      }
    }
  }
}

.time-picker{
  margin-top: 6.5em;
  display: grid;
  grid-template-columns: repeat(5,calc(20% - .5em));
  gap: .625em;
  &__item{
    cursor: pointer;
    @include center;
    color: #737F8E;
    font-weight: 600;
    border-radius: 1.25em;
    background-color: $white;
    height: 2.75em;
    transition: all .3s;
    &:hover{
      color: $primary;
    }
    span{
      font-size: 1.125em;
    }
    &--disabled{
      pointer-events: none;
      color: rgba(#737F8E,.3);
    }
    &--selected{
      background-color: $primary;
      color: $white;
      pointer-events: none;
    }
  }
}


@media screen and (max-width: 992px){
  .appointment-doctors{
    margin-top: 2.1875em;
  }

  .appointment-card{
    grid-template-columns: repeat(2,calc(50% - .8125em));
    gap: 1.625em;
    padding-bottom: 1.5em;
    margin-bottom: 1.5em;
    &__col{
      grid-column: 1/-1;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__img{
      order: 2;
      margin: 0 0 0 1em;
      width: 3.75em;
      height: 3.75em;
    }
    &__name{
      font-size: 1em;
      line-height: 110%;
      font-weight: 500;
    }
    &__prof{
      font-size: .625em;
      margin-top: .6em;
    }
  }

  .date-card{
    padding: 1.5em .75em;
    min-height: unset;
    &__title{
      font-size: 1.25em;
    }
    .air-datepicker{
      margin-top: 1.33em;
      font-size: .75em;
    }
  }

  .time-picker{
    font-size: .75em;
    margin-top: 2.75em;
  }
}


@media screen and (max-width: 576px){
  .appointment-card{
    grid-template-columns: 100%;
    gap: 1.25em;
  }
}
