.service-group{
  color: $black;
  &__grid{
    margin-top: 1.625em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
  }
}

.service-card2{
  padding: 1.5em 1.25em 1.25em;
  min-height: 15.625em;
  background-color: $primaryLight;
  border-radius: 1.5625em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__title{
    font-weight: 500;
  }
  &__price{
    font-weight: 600;
  }
  &__btn{
    padding: 0 1.375em;
    height: 2.5em;
    span{
      font-size: .875em;
    }
  }
}

.articles{
  .text{
    margin-top: 1.875em;
    max-width: 68.75em;
    a{
      text-decoration: underline;
    }
    ul{
      li{
        line-height: 130%;
        padding-left: 1em;
      }
    }
  }
}

.doctors{
  padding-bottom: 7.5em;
  &._pb48{
    padding-bottom: 3em;
  }
  &._pt0{
    padding-top: 0;
  }
  &__grid{
    margin-top: 2.75em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.doctor-card{
  display: flex;
  &__img{
    width: 7.3125em;
    height: 7.3125em;
    margin-right: 1.25em;
  }
  &__content{
    padding-top: 2.5em;
  }
  &__name{
    font-size: 1.75em;
    line-height: 130%;
    font-weight: 500;
  }
  &__prof{
    margin-top: .25em;
    font-size: .9375em;
    line-height: 120%;
    font-weight: 500;
    color: $primary;
  }
  &__subtitle{
    margin-top: 1em;
    font-size: .9375em;
    line-height: 150%;
    font-weight: 600;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    margin: .5em .375em 0 0;
    padding: 0 1em;
    height: 1.875em;
    display: flex;
    align-items: center;
    border-radius: 1.25em;
    background-color: #E3F4F4;
    color: $primary;
    font-weight: 500;
    span{
      font-size: .75em;
    }
  }
  &__nav{
    margin-top: 1.75em;
    display: flex;
  }
  &__btn{
    margin-right: .75em;
    span{
      font-size: .875em;
    }
  }
  @media screen and (min-width: 993px){
    .arrow-link{
      min-width: unset;
      padding: 0 2.375em;
      svg{
        display: none;
      }
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .service-group{
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
  }

  .service-card2{
    padding: 1.375em 1em;
    min-height: 11.5em;
    border-radius: 1.25em;
    &__title,
    &__price{
      font-size: 1.125em;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      margin: .625em 0 0;
      padding: 0 1em;
      height: 1.875em;
      span{
        font-size: .8125em;
      }
    }
  }

  .articles{
    .text{
      margin-top: .5em;
    }
  }

  .doctors{
    padding-bottom: 5em;
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1.5em 2em;
    }
  }

  .doctor-card{
    display: flex;
    &__img{
      width: 5em;
      height: 5em;
      margin-right: .75em;
    }
    &__content{
      padding-top: 1.25em;
    }
    &__name{
      font-size: 1em;
      line-height: 110%;
    }
    &__prof{
      margin-top: .5em;
      font-size: .6875em;
    }
    &__subtitle{
      display: none;
    }
    &__items{
      margin-top: .75em;
    }
    &__item{
      margin: .3125em .1875em 0 0;
      padding: 0 .625em;
      height: 1.25em;
      border-radius: 1.25em;
      span{
        font-size: .6875em;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      display: none;
    }
    .arrow-link{
      min-width: unset;
      width: 100%;
      height: 2.5em;
      padding: 0 .875em;
      span{
        font-size: .8125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .service-group{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .service-card2{
    padding: 1.25em .75em .75em;
    min-height: 10.625em;
    &__title,
    &__price{
      font-size: 1em;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      padding: 0 .875em;
      height: 1.5625em;
      span{
        font-size: .6875em;
      }
    }
  }

  .articles{
    &._hide-mob{
      display: none;
    }
  }

  .doctors{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
  }
}
