.text{
  color: #5B5F74;
  hr{
    height: 2.5em;
    display: block;
    opacity: 0;
    margin: 0;
  }
  h3{
    margin-bottom: .32em;
    color: $black;
  }
  h4{
    font-size: 1.5em;
    line-height: 120%;
    font-weight: 600;
    margin-bottom: 1em;
  }
  h5{
    font-size: 1.1875em;
    line-height: 145%;
    font-weight: 600;
    margin-bottom: 1em;
  }
  li,
  p{
    font-size: 1.0625em;
    line-height: 150%;
  }
  p{
    @include lastM(0 0 1.625em);
  }
  ol,ul{
    @include grid100gap(.75em);
    @include lastM(0 0 1.75em);
    li{
      padding-left: 1.47em;
      position: relative;
      &:before{
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
  ol{
    counter-reset: list;
    li{
      counter-increment: list;
      &:before{
        content: counter(list) '. ';
        color: $primary;
      }
    }
  }
  ul{
    li{
      &:before{
        content: '';
        width: .3em;
        height: .3em;
        top: .5em;
        background-color: $primary;
        border-radius: 50%;
      }
    }
  }
  ul,ol{
    &._col2{
      @media screen and (min-width: 577px){
        display: block;
        column-count: 2;
        li{
          margin-bottom: .75em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text {
    h4{
      font-size: 1.125em;
    }
    h5{
      font-size: 1.0625em;
    }
    hr{
      height: 1.125em;
    }
    li,
    p{
      font-size: .9375em;
    }
    ol,ul,
    p{
      @include lastM(0 0 1em);
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    h4,
    h3{
      font-size: 1.5625em;
    }
  }
}
