.appointment{
  padding-bottom: 7.5em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
  }
}

.appointment-search{
  padding: 1.25em;
  background-color: $primaryLight;
  border-radius: 1.5625em;
  .fg{
    input{
      background-color: $white;
      border-radius: .625em;
    }
  }
  &__btn{
    position: absolute;
    top: 50%;
    right: 1em;
    transform: translateY(-50%);
    width: 1.1875em;
    @include center;
    img{
      width: 100%;
      display: block;
    }
  }
}

.service-card3{
  min-height: 12.5em;
  border-radius: 1em;
  background-color: $primaryLight;
  padding: 1.25em;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    color: $white;
    .service-card3__btn{
      background-color: $white;
      color: $primary;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 500;
  }
  &__icon{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    width: 4.375em;
    height: 4.375em;
    @include center;
    background-color: $white;
    border-radius: 50%;
    img{
      max-width: 80%;
      max-height: 80%;
    }
  }
  &__btn{
    padding: 0 1em;
    height: 2.5em;
    span{
      font-size: .875em;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .appointment{
    padding-bottom: 5em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    }
  }

  .appointment-search{
    padding: 0;
    background-color: transparent;
  }

  .service-card3{
    border-radius: 1.25em;
    padding: 1.125em 1em;
    justify-content: unset;
    &__icon{
      position: static;
      margin-bottom: 1.5em;
    }
    &__title{
      padding-bottom: .5em;
    }
    &__btn{
      margin-top: auto;
      height: 1.875em;
      span{
        font-size: .8125em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .appointment{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .6875em / 2));
      gap: .6875em;
    }
  }

  .service-card3{
    min-height: 10.625em;
    padding: .75em;
    &__icon{
      width: 3.75em;
      height: 3.75em;
      margin-bottom: 1.75em;
    }
    &__title{
      font-size: 1em;
      padding-bottom: .75em;
    }
    &__btn{
      height: 1.5625em;
      padding: 0 .875em;
      span{
        font-size: .6875em;
      }
    }
  }
}
