.footer{
  padding: 4em 0 1.25em;
  background-color: #222222;
  color: $white;
  &__top,
  &__bottom{
    display: grid;
    grid-template-columns: 40em calc(100% - 41.25em);
    gap: 1.25em;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__logo{
    display: block;
    width: 10.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    margin-top: auto;
    padding-bottom: 1.5em;
  }
  &__bottom{
    margin-top: 3.75em;
  }
  &__c,
  &__conf{
    font-size: .9375em;
    line-height: 170%;
    color: #8A8A8A;
  }
}

.footer-nav{
  margin-top: 6.25em;
  &:first-child{
    margin-top: 0;
  }
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: .5625em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 130%;
    color: #8A8A8A;
  }
  &__hidden{
    margin-top: 1.5em;
  }
  &__links{
    column-count: 3;
    column-gap: 1.25em;
  }
  &__link{
    font-size: 1.25em;
    line-height: 130%;
    text-decoration: underline;
    display: inline-block;
    width: 100%;
    margin-bottom: 1em;
    color: #E2E2E2;
  }
}

.footer-contacts{
  margin-top: 5.5em;
  &__phone{
    font-size: 1.875em;
    line-height: 170%;
  }
  &__info{
    margin-top: .5em;
    @include grid100gap(1.125em);
    &-item{
      font-size: 1.125em;
      line-height: 140%;
      color: #8A8A8A;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
  &__btn{
    &:hover{
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0 1.5em;
    &__top{
      grid-template-columns: 15em calc(100% - 15.5em);
      gap: .5em;
    }
    &__logo{
      width: 7.75em;
    }
    &__bottom{
      margin-top: 1.75em;
      justify-content: space-between;
      display: flex;
      gap: unset;
    }
    &__c,
    &__conf{
      font-size: .8125em;
      line-height: 130%;
    }
  }

  .footer-nav{
    margin-top: 3em;
    &__title{
      font-size: .9375em;
    }
    &__hidden{
      margin-top: 1em;
    }
    &__links{
      column-count: 2;
    }
    &__link{
      font-size: .875em;
      @media screen and (min-width: 577px){
        &:nth-child(14)~.footer-nav__link{
          display: none;
        }
      }
    }
  }

  .footer-contacts{
    &__phone{
      font-size: 1.3125em;
      line-height: 140%;
    }
    &__info{
      margin-top: .75em;
      gap: .75em;
      &-item{
        font-size: .8125em;
        line-height: 135%;
        font-weight: 500;
      }
    }
    &__nav{
      margin-top: 1.15em;
    }
    &__btn{
      height: 2.5em;
      padding: 0 1.625em;
      span{
        font-size: .8125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-top: 2.5em;
    &__inner{
      position: relative;
      padding-top: 6.625em;
    }
    &__top{
      grid-template-columns: 100%;
      gap: 2.125em;
    }
    &__col2{
      order: -1;
    }
    &__logo{
      position: absolute;
      top: 0;
      left: 0;
    }
    .socials{
      padding: 1.5em 0 0;
    }
    &__bottom{
      margin-top: 2.1875em;
      @include grid100gap(1.25em);
      &-col2{
        order: -1;
      }
    }
  }

  .footer-nav{
    margin-top: 1em;
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding-bottom: 1.25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #333;
    }
    &__title{
      font-size: 1.125em;
      color: $white;
      font-weight: 500;
    }
    &__btn{
      @include center;
      width: 1em;
      height: 1em;
      margin-left: .25em;
      svg{
        width: 100%;
      }
    }
    &__hidden{
      display: none;
    }
    &__links{
      column-count: 1;
    }
  }

  .footer-contacts{
    margin-top: 0;
    &__nav{
      display: none;
    }
  }
}
