.article-page{
  padding-bottom: 7.5em;
  .text{
    max-width: 66em;
    img{
      width: 100%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
  &__back{
    font-size: 1.0625em;
    line-height: 145%;
    text-decoration: underline;
    color: $primary;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .article-page{
    padding-bottom: 5em;
    &__back{
      font-size: .9375em;
    }
  }
}


