.news{
  &__inner{
    position: relative;
  }
  .arrow-link{
    display: inline-flex;
    margin-top: 1.25em;
  }
}

.news-slider{
  margin-top: 1.875em;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
      width: calc(100% / 3);
    }
    &-nav{
      position: absolute;
      right: 0;
      top: 0;
    }
    &-button{
      margin: 0 0 0 .5em;
      position: static;
    }
    &-pagination{
      display: none;
    }
  }
}

.blog-card{
  &__img{
    @include imgCover(57.5%);
    border-radius: 1.25em;
    margin-bottom: 1.25em;
  }
  &__date{
    display: inline-flex;
    align-items: center;
    height: 1.5625em;
    padding: 0 .625em;
    border: 1px solid $primary;
    border-radius: 2.5em;
    color: $primary;
    font-weight: 600;
    span{
      font-size: .75em;
    }
  }
  &__content{
    margin-top: .875em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 130%;
    font-weight: 500;
    @include truncate(3);
  }
  &__desc{
    font-size: 1em;
    line-height: 130%;
    font-weight: 500;
    color: #868A9D;
    margin-top: .75em;
  }
}

.main-slider{
  overflow: hidden;
  position: relative;
  .swiper{
    &-pagination{
      bottom: .75em;
      width: 100%;
      min-height: .625em;
      &-bullet{
        margin: 0 .5625em !important;
        background-color: $white;
        &-active{
          width: .625em;
          height: .625em;
        }
      }
    }
  }
}

.main-slide{
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0 0 3.125em 3.125em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__inner{
    padding: 15em 0 7.375em;
    min-height: 53em;
    position: relative;
    z-index: 2;
  }
  &__content{
    padding: 4em 1.875em 2em;
    background-color: $white;
    border-radius: 3.125em;
    width: 37.75em;
    max-width: 100%;
  }
  &__desc{
    font-size: 1.3125em;
    line-height: 140%;
    color: #818494;
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 3.75em;
  }
  .arrow-link{
    min-width: 18.5em;
    span{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .news{
    .arrow-link{
      width: 100%;
      margin-top: 1.625em;
    }
  }

  .news-slider{
    margin-top: 1.375em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 50%;
        padding-right: 1em;
      }
      &-nav{
        display: none;
      }
      &-pagination{
        display: flex;
        margin-top: .75em;
        position: static;
      }
    }
  }

  .blog-card{
    &__img{
      border-radius: .625em;
      margin-bottom: 1.125em;
    }
    &__date{
      height: 1.5em;
      span{
        font-size: .6875em;
      }
    }
    &__content{
      margin-top: .75em;
    }
    &__title{
      font-size: .9375em;
    }
    &__desc{
      font-size: .8125em;
    }
  }

  .main-slide{
    &__img{
      border-radius: 0 0 1.875em 1.875em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__inner{
      padding: 11.5em 0 8.25em;
      min-height: 40.625em;
    }
    &__content{
      padding: 2.25em 1.25em 1.75em;
      border-radius: 1.875em;
      width: 21.25em;
    }
    &__desc{
      font-size: .9375em;
      line-height: 130%;
      margin-top: .5em;
    }
    &__nav{
      margin-top: 2.75em;
    }
    .arrow-link{
      min-width: 100%;
      span{
        font-size: .9375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .news-slider{
    margin-top: .75em;
    .swiper-slide{
      width: 100%;
    }
  }

  .main-slide{
    &__img{
      border-radius: 0 0 1.5625em 1.5625em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__inner{
      padding: 5.75em 0 2em;
      min-height: 43.75em;
    }
    &__content{
      padding: 1.5625em 1.375em 1.875em;
      width: 100%;
    }
    &__desc{
      margin-top: .25em;
    }
    &__nav{
      margin-top: 1em;
    }
    .arrow-link{
      min-width: 14.5em;
      padding: 0 .9375em;
      height: 2.5em;
    }
  }
}
