.doctor-item{
  padding-bottom: 3em;
  &__grid{
    display: grid;
    grid-template-columns: 41em calc(100% - 41.625em);
    gap: 2.1875em .625em;
  }
  &__col2{
    padding-top: 2.875em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__img{
    padding: 1.25em 1.25em 0;
    height: 40.625em;
    background-color: $primaryLight;
    border-radius: 2.1875em;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: sticky;
    top: 1em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__name{
    font-size: 4.375em;
    line-height: 105%;
    font-weight: 500;
  }
  &__prof{
    font-size: 1em;
    line-height: 120%;
    color: $primary;
    font-weight: 500;
  }
  .doctor-card__subtitle{
    margin-top: 1.67em;
  }
  @media screen and (min-width: 993px){
    .doctor-card__item{
      padding: 0 1em;
      height: 2.5em;
      font-weight: 600;
    }
  }
  .arrow-link{
    margin-top: 2.75em;
    height: 3.75em;
    min-width: 31.25em;
    span{
      font-size: 1.0625em;
    }
  }
  .text{
    color: #4B4E60;
    margin-top: 2.1875em;
  }
}


@media screen and (max-width: 992px){
  .doctor-item{
    padding-bottom: 2em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      padding: 0;
    }
    &__img{
      padding: .5em 1.5em 0;
      height: 31.25em;
      position: static;
    }
    &__name{
      font-size: 1.875em;
      line-height: 110%;
    }
    &__prof{
      font-size: .8125em;
    }
    .arrow-link{
      margin-top: 1.875em;
      height: 3.125em;
      min-width: unset;
      width: 100%;
      span{
        font-size: .9375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .doctor-item{
    &__grid{
      gap: 1.25em;
    }
    &__img{
      padding: 0;
      height: 22em;
    }
    .text{
      margin-top: 1.875em;
    }
  }
}
