.desktop-menu{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  background: #e3e6f3;
  z-index: 10;
  overflow-y: auto;
  color: #5b5f74;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    min-height: $vh;
    padding-top: 12em;
    padding-bottom: 3em;
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    display: grid;
    grid-template-columns: 27em  calc(100% - 32.5em);
    justify-content: space-between;
    margin-top: auto;
    padding-top: 2em;
    &-col{
      display: flex;
      align-items: center;
    }
    &-col2{
      display: grid;
      grid-template-columns: 50% 50%;
      align-items: center;
    }
  }
  &__phone{
    font-weight: 500;
    font-size: 1.875em;
    line-height: 170%;
    white-space: nowrap;
  }
  &__time{
    font-weight: 500;
    font-size: .6875em;
    line-height: 120%;
    margin-left: 1.5em;
  }
  .socials{
    &__item{
      width: 2.5em;
      height: 2.5em;
      background-color: #7b7f94;
      path{
        fill: $white;
      }
      svg{
        width: 1.25em;
      }
    }
  }
  &__btn{
    display: none;
  }
  &__address{
    font-size: .875em;
    line-height: 140%;
    color: #747992;
  }
  &__close{
    position: absolute;
    top: 2.5em;
    right: 2.5em;
    width: 3.875em;
    height: 3.875em;
    background-color: $white;
    border-radius: 50%;
    @include center;
    svg{
      width: 1.125em;
    }
  }
}

.nav-item2__sub-link,
.nav2__link,
.nav-item2__back span,
.nav-item2__show span{
  font-size: 1.4375em;
  line-height: 170%;
}

.nav-item2{
  margin-bottom: 1.5em;
  &._active{
    .nav-item2__hidden{
      opacity: 1;
      pointer-events: unset;
    }
    .nav-item2__show{
      color: $primary;
      svg{
        opacity: 1;
      }
      path{
        stroke: $primary;
      }
    }
  }
  &__back{
    display: none;
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      color: $primary;
    }
    svg{
      width: .5em;
      margin-left: .5em;
      opacity: 0;
    }
  }
  &__hidden{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    right: 0;
    top: 0;
    width: calc(100% - 32.5em);
  }
  &__links{
    column-count: 2;
  }
  &__sub-link{
    display: inline-block;
    width: 100%;
    font-weight: 400;
    margin-bottom: 1em;
  }
}

.nav2{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-weight: 500;
  color: #494d63;
  position: relative;
  min-height: 39em;
  &__link{
    margin-bottom: 1em;
    max-width: 15.6em;
  }
}


@media screen and (max-width: 992px){
  .desktop-menu{
    &__inner{
      padding-top: 5em;
      padding-bottom: 2em;
    }
    &__bottom{
      padding-top: 1em;
      grid-template-columns: 17em calc(100% - 17em);
      &-col{
        flex-direction: column;
        align-items: flex-start;
      }
    }
    &__phone{
      font-size: 1.25em;
    }
    &__time{
      font-size: .5625em;
      margin: 0;
    }
    .socials{
      &__item{
        width: 2.25em;
        height: 2.25em;
        svg{
          width: 1.0625em;
        }
      }
    }
    &__address{
      font-size: .625em;
      line-height: 120%;
    }
    &__close{
      top: 1em;
      right: 1.25em;
      width: 3.125em;
      height: 3.125em;
      svg{
        width: 1em;
      }
    }
  }

  .nav-item2__sub-link,
  .nav2__link,
  .nav-item2__show span{
    font-size: 1.0625em;
  }

  .nav-item2{
    margin-bottom: 1em;
    &__hidden{
      width: calc(100% - 17em);
    }
  }

  .nav2{
    min-height: 31em;
  }
}


@media screen and (max-width: 576px){
  .desktop-menu{
    background-color: $primaryLight;
    position: fixed;
    top: 4em;
    height: calc(100 * var(--vh) - 4em);
    &__inner{
      padding-top: 0;
      padding-bottom: 1.875em;
      min-height: calc(100 * var(--vh) - 4em);
    }
    .container{
      padding: 0;
    }
    &__bottom{
      grid-template-columns: 100%;
      gap: .625em;
      padding: 2.375em .75em 0;
    }
    &__phone{
      font-size: 1.6875em;
    }
    &__time{
      display: none;
    }
    .socials{
      &__item{
        width: 3.125em;
        height: 3.125em;
        background-color: transparent;
        border: 1px solid $primary;
        svg{
          width: 1.5em;
        }
        path{
          fill: $primary;
        }
      }
    }
    &__btn{
      display: inline-flex;
      margin-right: auto;
      margin-top: 1.5em;
    }
    &__address{
      display: none;
    }
    &__close{
      display: none;
    }
  }

  .nav-item2__sub-link,
  .nav2__link,
  .nav-item2__back span,
  .nav-item2__show span{
    font-size: 1em;
  }

  .nav-item2__back,
  .nav-item2__sub-link,
  .nav2__link,
  .nav-item2__show{
    height: 4.125em;
    display: flex;
    align-items: center;
    padding: 0 .75em;
    border-bottom: 1px solid #D0F0EE;
    max-width: 100%;
    width: 100%;
    margin: 0;
  }

  .nav2{
    min-height: unset;
    width: 100%;
    font-weight: 400;
    color: #5B5F74;
    position: static;
    ._hide-mob{
      display: none;
    }
    &__link{

    }
  }

  .nav-item2{
    width: 100%;
    margin: 0;
    &._active{
      .nav-item2__hidden{
        overflow-y: auto;
        height: calc(100 * var(--vh) - 4em);
      }
    }
    &__show{
      svg{
        opacity: 1;
        margin-left: auto;
      }
      path{
        stroke: #5B5F74;
      }
    }
    &__hidden{
      position: absolute;
      width: 100%;
      min-height: 100%;
      overflow: hidden;
      background-color: $primaryLight;
      height: 0;
    }
    &__links{
      column-count: 1;
    }
    &__back{
      svg{
        width: .375em;
        margin-right: .25em;
      }
    }
  }
}
