@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.75em;
    height: 3.5em;
    border-radius: 1.875em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.0625em;
    }
}

.m-btn-small{
    height: 3.125em;
    padding: 0 2.25em;
    span{
        font-size: 1em;
    }
}

.m-btn-white{
    color: $gray;
    background-color: $white;
    &:hover{
        color: $white;
        background-color: $black;
    }
}

.m-btn-primary{
    color: $white;
    background-color: $primary;
    &:hover{
        color: $white;
        background-color: $black;
    }
}

.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        border-color: $black;
        background-color: $black;
        color: $white;
        ._stroke{
            stroke: $white;
        }
        ._fill{
            fill: $white;
        }
    }
}

.m-btn-gray-outline{
    border: 1px solid #B0B4C7;
    color: #B0B4C7;
    &:hover{
        border-color: $black;
        background-color: $black;
        color: $white;
        ._stroke{
            stroke: $white;
        }
        ._fill{
            fill: $white;
        }
    }
}
