.page-top{
    padding-top: 3.125em;
    padding-bottom: 1.875em;
    .h1{
        margin-left: -.1em;
    }
}

.breadcrumbs {
    color: $primary;
    font-weight: 500;
    padding: .25em 0;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .4375em;
                right: 100%;
                margin-right: .625em;
                width: .375em;
                height: .5em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
            }
        }
        &:last-child{
            pointer-events: none;
        }
    }
    a{
        font-size: .8125em;
        line-height: 140%;
        &:hover{
            color: $black;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding-top: 1em;
        padding-bottom: .5em;
    }

    .breadcrumbs {
        margin-bottom: .5em;
        li{
            font-size: .92em;
        }
    }
}


@media screen and (max-width: 576px){
    .page-top{
        padding-top: .75em;
        padding-bottom: 1.625em;
    }

    .breadcrumbs {
        li{
            font-size: .77em;
        }
    }
}
