.contacts{
  padding-top: 0;
  &__grid{
    display: grid;
    grid-template-columns: auto 35.5em;
    justify-content: space-between;
    gap: 2.1875em;
  }
  &__phone{
    font-size: 3.75em;
    line-height: 145%;
    font-weight: 500;
  }
  &__desc{
    font-size: 1.0625em;
    line-height: 145%;
    font-weight: 500;
    color: #777A8D;
  }
  .socials{
    margin-top: 2.375em;
  }
  &__info{
    &-item{
      font-size: 1.0625em;
      line-height: 140%;
      color: #66697C;
      font-weight: 500;
      a{
        color: $primary;
        text-decoration: underline;
      }
    }
  }
  &__address{
    margin-top: 2.5em;
    font-size: 1.125em;
    line-height: 145%;
    color: #4B4E60;
    b{
      font-weight: 600;
    }
  }
  .map{
    margin-top: 1.75em;
  }
  &__text{
    margin-top: 2.125em;
    max-width: 64.5em;
    p{
      font-size: 1.1875em;
    }
  }
}

.map{
  border-radius: 2.1875em;
  overflow: hidden;
  height: 37.5em;
}


@media screen and (max-width: 992px){
  .contacts{
    padding-top: 1em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__phone{
      font-size: 2.5em;
    }
    &__desc{
      font-size: .875em;
    }
    .socials{
      margin-top: 2.25em;
    }
    &__info{
      &-item{
        font-size: .9375em;
        line-height: 150%;
      }
    }
    &__address{
      margin-top: 1.33em;
      font-size: .9375em;
    }
    .map{
      margin-top: 1.25em;
    }
    &__text{
      margin-top: 1.625em;
      p{
        font-size: .9375em;
      }
    }
  }

  .map{
    height: 25em;
    border-radius: 1.5625em;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    padding-top: 0;
    &__grid{
      gap: 1.75em;
    }
    &__phone{
      font-size: 1.875em;
    }
    &__desc{
      font-size: .75em;
    }
    .socials{
      margin-top: 1.5em;
    }
    .socials{
      &._type2{
        .socials__item{
          width: 3.125em;
          height: 3.125em;
          svg{
            width: 1.625em;
          }
        }
      }
    }
    &__info{
      &-item{
        font-size: .875em;
      }
    }
    &__address{
      margin-top: 1.43em;
      font-size: .875em;
    }
    .map{
      margin-top: 1em;
    }
    &__text{
      margin-top: 2.1875em;
      p{
        font-size: .875em;
      }
    }
  }

  .map{
    height: 34.375em;
    border-radius: 1.25em;
  }
}
