.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: .88em;
    border: 1px solid #CDCED8;
    padding: 0 1.0625em;
    height: 3.25em;
    width: 100%;
    font-size: 1.0625em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #8D909E;
    }
    &:focus {
      border-color: $primary;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1em;
    padding-bottom: 1em;
    height: 7em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: .625em;
    white-space: nowrap;
    height: 3.75em;
    padding: 0 1em;
    border-width: 2px;
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 1.125em;
      height: 1.125em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
      font-weight: 600;
    }
    .list{
      border-radius: .625em;
      width: 100%;
      border: none;
      background: $primaryLight;
      box-shadow: none;
      max-height: 31.25em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
      padding: 1.5em 0;
    }
    .option{
      height: unset;
      font-size: 1em;
      line-height: 157%;
      padding: .125em 1em;
      display: flex;
      align-items: center;
      color: #7b7f98;
      font-weight: 500;
      border: none;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._white{
    input,
    textarea{
      background-color: $white;
      border-color: $white;
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.375em;
    min-height: 1em;
    font-weight: 500;
    color: #8D8F9E;
    span{
      font-size: .8125em;
      line-height: 130%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #8D8F9E;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: .625em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #E9EDF2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #E9EDF2;
    border-right: 1px solid #E9EDF2;
    color: $black;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    @media screen and (min-width: 577px){
      input,
      textarea{
        font-size: .8125em;
        padding: 0 1em;
        height: 3.46em;
        border-radius: .77em;
      }
      textarea{
        height: 8.6em;
        padding-top: 1.125em;
        padding-bottom: 1.125em;
      }
    }
    .nice-select{
      font-size: .83em;
    }
  }
}


@media screen and (max-width: 576px){
  .fg{
    input,
    textarea{
      font-size: 1em;
    }

  }
}
