.grid{
  display: grid;
  gap: 1.125em;
  grid-template-columns: 100%;
}

.grid2{
  grid-template-columns: repeat(2,calc(50% - .5625em));
}

.grid3{
  grid-template-columns: repeat(3,calc(100% / 3 - 2.25em / 3));
}

.grid4{
  grid-template-columns: repeat(4,calc(25% - 3.375em / 4));
}

.grid5{
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
}


@media screen and (max-width: 992px){
  .grid{
    gap: 1em;
  }

  .grid-tablet{
    grid-template-columns: 100%;
  }

  .grid-tablet2{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }

  .grid-tablet3{
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
  }
}


@media screen and (max-width: 576px){
  .grid{
    gap: .625em;
  }

  .grid-mob{
    grid-template-columns: 100%;
  }

  .grid-mob2{
    grid-template-columns: repeat(2,calc(50% - .3125em));
  }
}
