.worker-card{
  position: relative;
  &:hover{
    .worker-card__timetable{
      opacity: 1;
    }
  }
  &__img{
    background: #d1e2e2;
    border-radius: .9375em;
    padding-bottom: 145%;
    position: relative;
    img{
      max-width: 100%;
      max-height: 100%;
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
    }
  }
  &__content{
    left: .625em;
    bottom: .625em;
    width: calc(100% - 1.25em);
    background-color: $white;
    border-radius: 1.5em;
    padding: 1em 1.25em;
    position: absolute;
  }
  &__name{
    font-size: 1.25em;
    line-height: 124%;
    font-weight: 500;
  }
  &__desc{
    font-size: 1.0625em;
    line-height: 120%;
    font-weight: 500;
    color: $gray;
    margin-top: .75em;
  }
  &__timetable{
    transition: all .3s;
    position: absolute;
    z-index: 2;
    background-color: $primary;
    border-radius: .9375em;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    color: $white;
    padding: 2em 1.5em;
    display: flex;
    flex-direction: column;
    opacity: 0;
  }
  &__title{
    font-weight: 500;
    font-size: 1.875em;
    line-height: 110%;
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.875em);
  }
  &__item{
    display: grid;
    grid-template-columns: auto 10.5em;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    &._empty{
      .worker-card__item-time{
        border: 1px solid #11c7c2;
        background-color: transparent;
      }
    }
    &-day{
      font-size: 1.125em;
      line-height: 135%;
    }
    &-time{
      background-color: $white;
      border-radius: 2.5em;
      height: 2.5em;
      @include center;
      color: #737f8e;
      span{
        font-size: 1.125em;
      }
    }
  }
  &__nav{
    padding-top: 1em;
    margin-top: auto;
  }
}


@media screen and (max-width: 992px){
  .worker-card{
    &__content{
      padding: .75em;
      border-radius: .9375em;
    }
    &__name{
      font-size: 1em;
      line-height: 110%;
    }
    &__desc{
      font-size: .8125em;
    }
    &__timetable{
      padding: 1.25em;
      font-size: .625em;
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .worker-card{
    &__img{
      padding-bottom: 135%;
      border-radius: 1.25em;
    }
    &__content{
      position: static;
      padding: .625em 0 0;
      width: 100%;
    }
    &__desc{
      margin-top: .5em;
    }
  }
}
